.formTitle,
.formTitleSubtext {
  align-self: stretch;
  position: relative;
}
.formTitleSubtext {
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.formTitle {
  font-size: var(--font-size-11xl);
  color: var(--color-darkslategray-100);
}
.formHeadingSection {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-6xs);
}
.formDescription {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-lg);
  line-height: 34px;
  font-family: var(--font-roboto);
  color: var(--color-slategray);
  text-align: left;
}
.formText {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.avatar,
.op {
  align-items: center;
  justify-content: center;
}
.op {
  align-self: stretch;
  flex: 1;
  position: relative;
  font-size: var(--font-size-xs);
  letter-spacing: 0.4px;
  line-height: 166%;
  font-family: var(--font-roboto);
  color: var(--grey-grey-700);
  text-align: center;
  display: flex;
}
.avatar {
  border-radius: var(--br-45xl);
  background-color: var(--grey-grey-400);
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
  display: none;
  flex-direction: row;
}
.chip1 {
  position: relative;
  font-size: var(--font-size-smi);
  letter-spacing: 0.16px;
  line-height: 18px;
  font-family: var(--font-roboto);
  color: var(--text-primary);
  text-align: left;
}
.typography {
  display: flex;
  flex-direction: column;
  padding: var(--padding-10xs) var(--padding-7xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.cancelIcon {
  position: relative;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  overflow: hidden;
}
.container {
  border-radius: var(--br-base);
  background-color: var(--grey-grey-300);
  display: flex;
  flex-direction: row;
  padding: var(--padding-9xs);
  align-items: center;
  justify-content: flex-start;
}
.chip,
.muiautocompletetag {
  align-items: flex-start;
  justify-content: flex-start;
}
.chip {
  display: flex;
  flex-direction: column;
}
.muiautocompletetag {
  display: none;
  flex-direction: row;
  gap: var(--gap-5xs);
}
.yourName {
  flex: 1;
  position: relative;
  font-size: var(--font-size-base);
  letter-spacing: 0.15px;
  line-height: 24px;
  font-family: var(--font-roboto);
  color: var(--color-gray-200);
  text-align: left;
}
.closeIcon {
  position: relative;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
  overflow: hidden;
}
.iconbutton,
.unstylediconbutton {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.unstylediconbutton {
  border-radius: var(--br-29xl);
  flex-direction: row;
  padding: var(--padding-9xs);
}
.iconbutton {
  position: absolute;
  top: 0;
  left: 0;
  flex-direction: column;
}
.muiAutocompleteIndicator {
  position: relative;
  width: 28px;
  height: 28px;
  flex-shrink: 0;
  display: none;
}
.arrow,
.formFields,
.inactive,
.input {
  display: flex;
  justify-content: flex-start;
}
.arrow {
  flex-direction: row;
  align-items: flex-start;
}
.formFields,
.inactive,
.input {
  align-self: stretch;
}
.inactive {
  overflow: hidden;
  flex-direction: row;
  padding: var(--padding-base) 0;
  align-items: center;
  gap: var(--gap-5xs);
}
.formFields,
.input {
  flex-direction: column;
  align-items: flex-start;
}
.input {
  border-radius: var(--br-9xs);
  border: 1px solid var(--color-gray-300);
  padding: 0 var(--padding-xs);
}
.formFields {
  gap: var(--gap-3xs);
}
.submit {
  position: relative;
  font-size: var(--font-size-mini);
  letter-spacing: 0.46px;
  line-height: 26px;
  text-transform: uppercase;
  font-weight: 500;
  font-family: var(--font-roboto);
  color: var(--color-white);
  text-align: center;
}
.searchButton {
  cursor: pointer;
  border: 0;
  padding: 0 var(--padding-3xs);
  background-color: var(--color-orange);
  border-radius: var(--br-9xs);
  width: 164px;
  height: 56px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.searchButton:hover {
  background-color: var(--color-darkgoldenrod-100);
}
.form {
  align-self: stretch;
  border-radius: var(--br-xs);
  background-color: var(--color-white);
  box-shadow: 0 0 24px rgba(0, 0, 0, 0.03);
  border: 1px solid var(--color-whitesmoke);
  display: flex;
  flex-direction: column;
  padding: var(--padding-9xl) var(--padding-11xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mid);
}
.shareYourTravelsForm,
.subscribeSection {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.shareYourTravelsForm {
  width: 549px;
  padding: var(--padding-xl);
  box-sizing: border-box;
  justify-content: flex-start;
  gap: var(--gap-11xl);
  margin-left: auto;
  margin-right: auto;
  position: relative !important;
  left: auto !important;
}
.subscribeSection {
  align-self: stretch;
  overflow: hidden;
  padding: var(--padding-33xl) var(--padding-xl);
  justify-content: center;
  background-image: url(/public/subscribe-section@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-cornflowerblue-300);
  font-family: var(--font-roboto);
}
@media screen and (max-width: 960px) {
  .shareYourTravelsForm {
    padding: var(--padding-5xl) 0;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .searchButton {
    width: 100%;
  }
  .shareYourTravelsForm {
    padding-left: var(--padding-5xl);
    padding-right: var(--padding-5xl);
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    position: relative !important;
    left: auto !important;
    width: 100%;
  }
}
